import _ from 'lodash';

export default class EmbedHelper {
  static async getAppWrapper(targetHtml: string): Promise<HTMLDivElement> {
    // Get DOM data
    const page = chrome.runtime.getURL(targetHtml);
    const htmlText = await (await fetch(page)).text();
    const doc = new DOMParser().parseFromString(htmlText, 'text/html');
    const appWrapper = document.createElement('div');

    // Append each DOM node to the page
    Array.from(doc.head.childNodes)
      .concat(Array.from(doc.body.childNodes))
      .forEach((el) => {
        appWrapper.append(el.cloneNode(true));
      });

    // Append each script tag to the page
    const scriptTags = doc.head.querySelectorAll('script');
    scriptTags.forEach((scriptTag) => {
      const script = document.createElement('script');

      Array.from(scriptTag.attributes).forEach((attribute) => {
        script.setAttribute(attribute.name, attribute.value);
      });

      appWrapper.append(script);
    });

    // Prepend element
    return appWrapper;
  }

  static observeUrlChange = (renderContent: () => Promise<void>) => {
    let oldPathname: null | string = null;
    const debouncedRenderContent = _.debounce(renderContent, 100);
    const observer = new MutationObserver(async () => {
      if (oldPathname !== document.location.pathname) {
        oldPathname = document.location.pathname;
        await debouncedRenderContent();
      }
    });
    observer.observe(document.body, { childList: true, subtree: true });
  };

  static REVIEWS_RATING_SELECTORS = {
    SEARCH_REVIEWS_ATTRIBUTE: '[data-csa-c-slot-id="alf-reviews"]',
    FEEDBACK_ID: '#adfeedbackdetails span[aria-label]',
    CAROUSEL_CARD_CLASSES: '.a-carousel-card .a-icon-row .a-size-small[aria-hidden="true"]'
  };

  static observeFullUrlChange = (renderContent: () => Promise<void>) => {
    let oldUrl: null | string = null;
    const debouncedRenderContent = _.debounce(renderContent, 100);
    const observer = new MutationObserver(async (mutations) => {
      if (oldUrl !== document.location.href) {
        oldUrl = document.location.href;
        await debouncedRenderContent();
      }

      const isSelectorUpdated = (addedNodes: NodeList, selector: string) =>
        Array.from(addedNodes).some((node) => node instanceof HTMLElement && node.querySelector(selector));

      const hasRelevantMutations = mutations.some((mutation) => {
        if (!mutation.addedNodes.length) {
          return false;
        }

        return Object.values(this.REVIEWS_RATING_SELECTORS).some((selector) =>
          isSelectorUpdated(mutation.addedNodes, selector)
        );
      });

      if (hasRelevantMutations) {
        await debouncedRenderContent();
      }
    });

    observer.observe(document.body, { childList: true, subtree: true });
  };
}
