import { SUPPORTED_MARKETPLACES } from '@seller-utilities/database';

import Routes from '../../utils/routes';
import EmbedHelper from '../utils/EmbedHelper';

const renderCharts = async () => {
  const HTML_FILE = `.${Routes.EMBEDS_ADV_CHART}.html`;
  const CHARTS_WRAPPER = document.querySelector('div[data-e2e-id="DASHBOARD_campaignsDashboard"]');

  const ADV_PAGE_URLS = Object.values(SUPPORTED_MARKETPLACES).map(
    (marketplace) => `https://advertising.amazon.${marketplace.urlSuffix}/cm/campaigns`
  );

  if (ADV_PAGE_URLS.some((advPageUrl) => window.location.href.startsWith(advPageUrl)) && CHARTS_WRAPPER) {
    const appWrapper = await EmbedHelper.getAppWrapper(HTML_FILE);
    CHARTS_WRAPPER.prepend(appWrapper);
  }
};

// render charts after script and styles are injected
window.addEventListener('load', () => EmbedHelper.observeUrlChange(renderCharts), false);
